.business-section-2 {
    .card-service {
        .icon-shape {
            opacity: 0;
            transition: all 0.3s ease-in-out;
        }

        &:hover {
            .icon-shape {
                opacity: 1;
                transition: all 0.3s ease-in-out;
            }
        }
    }
    @media screen and (max-width: 991.98px) {
        .card-service {
            .icon-shape {
                opacity: 1;
                transition: all 0.3s ease-in-out;
            }
        }
    }
}
body.business {
    --bs-border-radius-xl: 10px;
    --bs-border-radius-xxl: 20px;

    .bg-secondary-3 {
        background: #eceff1;
    }

    .button--calypso::before {
        background: #094cdf;
    }

    .button--calypso::after {
        background: #094cdf;
    }
}

//01-business-home
.business-section-4 {
    .img-project {
        .bg-button {
            opacity: 0;
            transition: all 0.3s ease-in-out;
        }
        .button {
            opacity: 0;
            transition: all 0.3s ease-in-out;
        }
        &:hover {
            .bg-button {
                filter: blur(20px);
                opacity: 1;
                transition: all 0.3s ease-in-out;
            }
            .button {
                opacity: 1;
                transition: all 0.3s ease-in-out;
            }
        }
        &.img-2 {
            width: 295px;
            height: 295px;
        }
        &.img-3 {
            width: 230px;
            height: 230px;
        }
        &.img-5 {
            width: 184px;
            height: 184px;
        }
    }
}
.business-section-9 {
    .bg-light-2 {
        clip-path: polygon(0 50%, 100% 0, 100% 100%, 0% 100%);
    }
}
.card-pricing {
    .sale-off {
        border-radius: 0px 0px 6px 6px;
        border: 1px solid var(--tc-theme-primary);
        border-top: 0;
        background: rgba(13, 110, 253, 0.1);
    }
}
.progress,
.progress-stacked {
    --bs-progress-height: 4px;
    --bs-progress-font-size: 0.75rem;
    --bs-progress-bg: #e9e9e9;
    --bs-progress-border-radius: 20px;
    --bs-progress-box-shadow: var(--bs-box-shadow-inset);
    --bs-progress-bar-color: #fff;
    --bs-progress-bar-bg: var(--tc-theme-primary);
    --bs-progress-bar-transition: width 0.6s ease;
    display: flex;
    height: var(--bs-progress-height);
    overflow: hidden;
    font-size: var(--bs-progress-font-size);
    background-color: var(--bs-progress-bg);
    border-radius: var(--bs-progress-border-radius);
}
// services-details
.bg-banner {
    background-repeat: no-repeat;
    background-size: cover;
    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: var(--tc-system-navy-blue-4);
        z-index: -1;
        border-radius: 0 !important;
    }
}
.card {
    transition: all 0.3s ease-in-out;
    .card-header {
        a {
            .arrow {
                &::before {
                    transition: 0.3s;
                    transform: rotate(0deg);
                    font-family: bootstrap-icons;
                    content: '\F2EA';
                    color: var(--tc-system-navy-blue-3);
                    font-size: 20px;
                }
            }
            border-bottom: 1px solid transparent;
            &.collapsed {
                border-bottom: 1px solid rgba(255, 255, 255, 0.2);
                .arrow {
                    background-color: transparent;
                    &::before {
                        transition: 0.3s;
                        color: var(--tc-system-navy-blue-3);
                        transform: rotate(180deg);
                        content: '\F4FE';
                    }
                }
            }
        }
    }
}
// testimonials
.business-pricing-section-3 {
    // Swiper customize
    .swiper-button-next,
    .swiper-button-prev {
        --swiper-navigation-size: 16px;
        --swiper-navigation-top-offset: 50%;
        --swiper-navigation-sides-offset: 10px;
        --swiper-navigation-color: var(--tc-system-black);
        display: flex;
        width: 50px;
        height: 50px;
        padding: 0px;
        font-size: 16px;
        border-radius: 50px;
        background-color: var(--tc-system-white) !important;
        border: 1px solid var(--tc-system-stroke);
        svg {
            width: 16px;
            height: 16px;
        }
        &:hover,
        :active {
            background-color: var(--tc-system-black) !important;
            border: 1px solid var(--tc-system-black);

            svg {
                path {
                    fill: var(--tc-system-white);
                }
            }
        }
    }
}
// contact
.business-contact-section-2 {
    .card-contact {
        .border-bottom {
            transition: all 0.2s ease-in-out;
        }
        &:hover {
            .border-bottom {
                border-color: var(--tc-theme-primary) !important;
                transition: all 0.2s ease-in-out;
            }
        }
    }
    .contact-map {
        max-height: 500px;
        iframe {
            height: 500px;
            width: 100%;
        }
    }
    .form-control:focus {
        box-shadow: none;
        border: 1px solid var(--tc-system-stroke) !important;
    }
}
.pagination_item {
    font-weight: 400 !important;
    background: white;
    color: var(--tc-system-black);
    border: 1px solid var(--tc-system-stroke) !important;
    border-radius: 10px !important;

    &:hover {
        background: var(--tc-theme-primary);
        color: var(--tc-system-white) !important;
        transition: 0.3s ease-in-out;
        border-color: var(--tc-theme-primary) !important;
        svg {
            path {
                stroke: var(--tc-system-white) !important;
                transition: 0.3s ease-in-out;
            }
        }
    }
    &.active {
        background: var(--tc-theme-primary);
        color: var(--tc-system-white) !important;
        transition: 0.3s ease-in-out;
        border-color: var(--tc-theme-primary) !important;
        svg {
            path {
                stroke: var(--tc-system-white) !important;
                transition: 0.3s ease-in-out;
            }
        }
    }
    &:focus {
        box-shadow: none;
        background-color: var(--tc-theme-primary) !important;
        border-color: var(--tc-theme-primary);
        color: var(--tc-system-white) !important;
        transition: 0.3s ease-in-out;
        svg {
            path {
                stroke: var(--tc-system-white) !important;
                transition: 0.3s ease-in-out;
            }
        }
    }
}
.input-group {
    .form-control {
        color: var(--tc-system-black);
        &:hover,
        &:focus {
            background-color: var(--tc-system-white) !important;
            border: 1px solid var(--tc-system-black) !important;
        }
    }
}
